<template>
	<div class="Inheader">
		<div class="Inheaderimg" @click="toureluser()">
			<img v-lazy="yyimg">
		</div>
		<h4></h4>
		<div class="Ininput">
		  <van-field
			v-model="data.username"
			class="Ininputst"
			clearable
			left-icon="manager"
			placeholder="//"
		  />
		  <van-field
			v-model="data.password"
			class="Ininputst"
			clearable
			left-icon="lock"
			type="password"
			placeholder=""
		  />
		</div>
		<van-button round  type="primary" color="#c85e55" block @click="toindex()" ></van-button>
		<div class="Innav">
			<p style="text-align:left;" @click="toregistered()"></p>
			<p style="text-align: right;" @click="toPassword()"></p>
		</div>
		<!--  -->
		<van-divider></van-divider>
		<div style="color: #999;font-size: 14px;">
			<p>/</p>
			1<br>
			2//<br>
			3/3400-006-0996</div>
	</div>
</template>

<script>
	import {
		Toast,Dialog 
	} from 'vant';
	export default {
	  data() {
	    return {
	      yyimg:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/yy.png',
		  data:{
			username: '',
			password: '',  
		  }
	    };
	  },
	  methods: {
		  // 
	    toindex(){
			if (this.data.username=='') {
				return Toast("!");  
			};
			if (this.data.password=='') {
				return Toast("!");  
			};
			let that = this;
			that.axios.post('v1/getContrarianLoginAccount',that.qs.stringify(that.data)).then((response)=>{
			    // ...
				if (response.data.code =='1000') {
					Toast("");
					// console.log(response);
					that.$uerstore.store.commit("INUserId",response.data.data.mem_id);//
					that.$uerstore.store.commit("INTOKENID",response.data.data.token);//TOEN
					that.$router.push({path:'/'});
				} else{
					Toast(response.data.msg);
				}
			})
		},
		//
		toregistered(){
			this.$router.push({path:'/registered'});
		},
		//
		toPassword(){
			this.$router.push({path:'/Password'});
		},
		//
		toureluser(){
			window.location.href = "https://zht.insooner.com/INContrarian";
		}
	  }
	};
</script>

<style scoped>
	.Innav{
		width:calc(100% - 80px);
		padding: 0 40px;
		overflow: hidden;
	}
	.Innav p{
		width: 50%;
		float: left;
		font-size: 12px;
	}
	.Inheader{
		width:calc(100% - 30px);
		padding: 15px;
		height: calc(100% - 30px);
		text-align: left;
	}
	.Inheaderimg{
		width: 100%;
		overflow: hidden;
	}
	.Inheaderimg img{
		width: 100%;
		display: block;
	}
	.Ininput{
		width:calc(100% - 40px);
		padding: 0 20px;
		margin-bottom: 40px;
	}
	.Ininputst{
		background-color: #F8F8F8;
		border-radius: 50px;
		margin-bottom: 10px;
		color: #C4C4C4;
	}
</style>



